.friendMessage {
    display: flex;
    flex-direction: column;
    margin-bottom: 25px;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    position: relative;
    z-index: 3;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    z-index: 3;
}

.friendText {
    position: relative;
    padding: 11px 13px 8px 17px;
    border-radius: 10px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {
    content: '';
    position: absolute;
    border-left: 20px solid #F5F7FB;
    top: 46px;
    left: -10px;
    width: 0px;
    height: 15px;
    z-index: 1;
    box-shadow: 0px 5px 20px rgb(29 33 38 / 3%), -4px 2px 3px -2px rgb(29 33 38 / 10%);
}

.friendText:before {
    content: '';
    position: absolute;
    background: #FFFFFF;
    border-radius: 100%;
    top: 33px;
    left: -25px;
    width: 25px;
    height: 28px;
    z-index: 2;
}

.friendName {
    font-weight: 600;
    font-size: 17px;
    line-height: 18px;
    letter-spacing: 2px;
}

.friendMessageText {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;

}

.friendTime {
    padding-left: 10px;
    font-weight: 600;
    font-size: 11px;
    line-height: 22px;
}