.burgerMenuIcon {
    width: 26px;
    height: 26px;
    margin-right: 10px;
    margin-left: 10px;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: calc(100% - 140px);
    height: 60px;
}

.headerWrapper {
    box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);

}

.header h1 {
    font-weight: 600;
    font-size: 26px;
    line-height: 29px;
}